import React, { memo, useState } from 'react'
import cn from 'classnames'

import { ReactComponent as DownIcon } from 'src/assets/images/icons/down.svg'

import styles from './styles.module.scss'

const AccordeonRaw = ({ title, rightPart, initialState = true, label, skin, content }) => {
  const [isOpened, setIsOpened] = useState(initialState)
  const onClickToggle = () => {
    setIsOpened(!isOpened)
  }

  return (
    <div
      className={cn(styles.acco, styles[`acco--${skin}`], {
        [styles.accoActive]: isOpened,
      })}
    >
      <div className={styles.head}>
        <div className={styles.left}>
          <div className={styles.top}>
            <div className={styles.title}>{title}</div>
            <div
              onClick={onClickToggle}
              className={cn(styles.toggle, {
                [styles.toggleActive]: isOpened,
              })}
            >
              {isOpened ? 'hide' : 'show'}
              <DownIcon className={styles.arrow} />
            </div>
          </div>
          {label && <div className={styles.label}>{label}</div>}
        </div>
        {rightPart && isOpened && <div className={styles.right}>{rightPart}</div>}
      </div>
      {isOpened && <div className={styles.body}>{content}</div>}
    </div>
  )
}

export const Accordeon = AccordeonRaw
export default memo(Accordeon)
