import React, { memo } from 'react'
import cn from 'classnames'

import { ReactComponent as HintIcon } from 'src/assets/images/icons/hint.svg'

import styles from './styles.module.scss'

const HintRaw = ({ content, isRight, isLeft }) => {
  return (
    <div className={styles.hint}>
      <div className={styles.head}>
        <HintIcon className={styles.icon} />
      </div>
      <div
        className={cn(styles.body, {
          [styles['body--left']]: isLeft,
          [styles['body--right']]: isRight,
        })}
      >
        <div className={styles.content} dangerouslySetInnerHTML={{ __html: content }} />
      </div>
    </div>
  )
}

export const Hint = HintRaw
export default memo(Hint)
