import React, { memo, Fragment } from 'react'

import { modalConstants } from 'src/constants'
import Accordeon from 'src/indyComponents/Accordeon'
import BaseButton from 'src/components/BaseButton'

import StatisticFilter from '../../components/StatisticFilter'
import MetricList from '../../components/MetricList'
import { useMappedActions } from './bindings'

const StatisticRaw = () => {
  const actions = useMappedActions()

  const onClickDelete = ({ presetName }) => {
    console.log('onClickDelete Statistic', presetName)
  }

  const onClickSave = ({ value }) => {
    console.log('onClickSave Statistic', value)
  }

  const onClick = () => {
    actions.showModal(modalConstants.DELETE_PRESET_MODAL, { presetName: 123132, onClickDelete })
  }

  const onClickS = () => {
    actions.showModal(modalConstants.SAVE_PRESET_MODAL, { onClickSave })
  }

  return (
    <Fragment>
      <StatisticFilter />
      <Accordeon title="Compressed metrics — table" initialState={true} content={<MetricList />} />
      <Accordeon title="Compressed metrics — graphic" skin="white" />
      <br />
      <br />
      <BaseButton text="DELETE_PRESET_MODAL" skin="yellow" disabled={false} onClick={onClick} />
      <br />
      <br />
      <BaseButton text="SAVE_PRESET_MODAL" skin="red" disabled={false} onClick={onClickS} />
    </Fragment>
  )
}

export const Statistic = StatisticRaw
export default memo(Statistic)
